.postsBlock {
    padding: 10px;
    border-top: 2px dotted lightgray;
    border-radius: 20px 20px 20px 20px;
}

.posts {
    margin: 20px 0 10px 0;
    padding: 10px;
    border-radius: 20px 20px 20px 20px;
}

.postsBlock textarea {
    width: 300px;
    height: 100px;
    border-radius: 20px;
    padding: 10px;
    font-size: 16px;
    /* background-color: lightgrey; */
    resize: none;    
}

